<template>
  <div id="overview-wrapper">
    <div id="overview-content">
      <div id="overview-img">
        <img :src="overview_img">
        <!-- <img v-if="datasetIndex!=0" class="clickable" src="@/assets/left-arrow.png" @click="setDatasetIndex(-1)" style="position: absolute; bottom: 45%; left: 35px; width: 70px; height: 70px; border-radius: 50px;"/>
        <img v-if="datasetIndex!=finding.Dataset.length-1" class="clickable" src="@/assets/right-arrow.png" @click="setDatasetIndex(+1)" style="position: absolute; bottom: 45%; right: 35px; width: 70px; height: 70px; border-radius: 50px;"/> -->
        <div id="overview-arnote">
          <span>{{time}}</span>
          <span>{{ar_note}}</span>
        </div>  
      </div>
      <div id="overview-input">
        <input type="text" name="" v-model="ar_title" placeholder="Untitled Inquiry" maxlength="50">
        <textarea name="" v-model="ar_description"  placeholder="Description (optional)" maxlength="200"></textarea>
      </div>
      <div id="overview-btn">
        <!-- <button id="overview-save">Save Draft</button> -->
        <button id="overview-publish" v-on:click="ar_publish()">Publish</button>
      </div>
    </div>
  </div>
	
</template>

<script>
  import axios from 'axios'
  import qs from 'qs'
  const api = axios.create({
    baseURL: 'https://kiwrious.org/api',
  })
  export default {
    data() {
      return {
        ar_imgsrc: this.$store.state.arimg,
        overview_img: this.$store.state.arimg[0],
        ar_label: this.$store.state.arlabel,
        ar_note: this.$store.state.arnote,
        ar_re: this.$store.state.arre,
        time: this.$store.state.time,
        ar_title: null,
        ar_description: null,
        uv: this.$store.state.uv,
        lux: this.$store.state.lux,
      }
    },
    methods: {
      ar_publish() {
        const url = '/finding';
        // const datasrc = '[{condition:"'+this.ar_label+'",uv:'+this.uv+',lux:'+this.lux+',media:"'+imgsrc+'"}]'
        const finding = {
          name: this.ar_title,
          description: this.ar_description,
          notes: this.ar_note,
          userId : sessionStorage.uid,
          inquiryId: '948a06f5-5f03-498a-9258-3f89ea1e2362',
          condition: this.ar_label,
          dataset: [],
          type: this.ar_re.type,
          base: this.ar_re.base,
          status: '2',
          similarity: '3',
        }
        let img_index = 0
        while(img_index < this.ar_imgsrc.length) {
          finding.dataset.push({
            condition: this.ar_label,
            uv: this.uv,
            lux: this.lux,
            media: this.ar_imgsrc[img_index].split(',')[1],
          })
          img_index++
        }
        console.log(finding)
        const config = {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        }
        api.post(url, qs.stringify(finding),config).then((response)=>{
          console.log('send')
          this.$router.push({path: '/home/myinquiries'})
        }).catch((e) => {
          console.log(e)
        });
      }
    },
  }
</script>

<style lang="scss">
  #overview-wrapper {
    background-color: #F4F6F9;
    height: 100%;
  }
  #overview-content {
    margin: auto;
    width: 60%;
    background-color: white;
    height: 100%;
  }
  #overview-img {
    width: 640px;
    height: 488px;
    margin: auto;
    padding-top: 18px;
    border-radius: 5px;
    overflow: hidden;
  }
  #overview-img img {
    display: block;
  }
  #overview-arnote {
    height: 88px;
    background-color: #E6EEEF;
  }
  #overview-arnote span {
    text-align: left;
    display: block;
    padding-left: 22px;
    padding-top: 14px;
    font-size: 18px;
  }
  #overview-input {
    width: 640px;
    margin: auto;
    input {
      width: 99%;
      border: 2px solid #E0E0E0;
      border-radius: 5px;
      height: 36px;
      margin-top: 16px;
      font-size: 20px;
    }
    textarea {
      width: 99%;
      border: 2px solid #E0E0E0;
      border-radius: 5px;
      height: 80px;
      margin-top: 16px;
      font-size: 18px;
      font-family: 'Arial';
    }
  }
  #overview-btn {
    padding-top: 20px;
    width: 640px;
    margin: auto;
    #overview-save {
      width: 144px;
      height: 40px;
      border-radius: 5px;
      font-size: 18px;
      font-weight: 600;
      color: #FA5708;
      background-color: white;
      border: 2px solid #FDB18B;
      float: left;
    }
    #overview-publish {
      width: 144px;
      height: 40px;
      border-radius: 5px;
      font-size: 18px;
      font-weight: 600;
      color: white;
      background-color: #FA5708;
      border: 2px solid #FDB18B;
      float: right;
      cursor: pointer;
    }
  }
</style>