<template>
	<div>
    <div>
    <el-row>
      <el-col :span="6" :offset="1" >
        <el-card style="position: relative;" class="box-card" shadow="hover" :body-style="{ padding: '0px' }" >
          <div style="width: 100%; height: 380px; background-color: #FFECE3;" v-on:click="jump('/find_ar')">
            <div style="width: 100%; height: 30%; vertical-align: top;"></div>
            <div style="width: 100%; height: 40%;vertical-align: top; font-size: 24px;">
              <img src="@/assets/add-btn.png" style="height: 50%; width: auto;"/>
              <p> New Project</p>
            </div>
            <div style="width: 100%; height: 30%; vertical-align: top;"></div>
          </div>          
        </el-card>
      </el-col>
      <el-col :span="6" v-for="finding in myFindings" :offset="1" v-bind:key="finding.Id">
        <el-card style="position: relative;" class="box-card" shadow="hover" :body-style="{ padding: '0px' }" @click.native="toFinding(finding)">
          <img v-bind:src="'https://kiwrious.org/Storage/'+ getMedia(finding)">
          <el-button v-if="finding.Status==1" class="draft-button" style="position: absolute; top: 10px; left: 10px;">DRAFT</el-button>
          <div class='box-description'>
            <div class="box-user"><i class="el-icon-user-solid"></i>{{finding.UserNickname}}</div>
              <div style="display: inline-block; width: 12%; text-align: right; font-size: 30px; font-weight: bold;">&#8942;</div>
            <div class="box-name">{{finding.Name}}</div>
          </div>
        </el-card>
      </el-col>
    </el-row>  
  </div>
  </div>
</template>

<script>
  import axios from 'axios'
  const api = axios.create({
    baseURL: 'https://kiwrious.org/api'
  })
  var finding_list = {}
  export default {
    data() {
      return {
        myFindings:[]
      }
    },
    created(){
      const userId = sessionStorage.uid;
      api.get('/finding/user/'+userId).then((response)=> {
        this.myFindings = response.data;
      });
    },
    beforeRouteEnter(to, from, next) {
      const uid = sessionStorage.uid
      const url = '/finding/user/' + uid
      api.get(url)
      .then((response) => {
        console.log(response)
        next()
      })
      .catch((error) => {
        console.log(error)
      })
    },
    methods: {
      jump(url) {
        this.$router.push({path: url})
      },
      getMedia(finding){
        if(finding.Dataset.length<this.datasetIndex){
          return "default-finding-image.jpg";
        }
        if(finding.Dataset[0].Media == null || finding.Dataset[0].Media == ""){
          return "default-finding-image.jpg";
        }
        return finding.Dataset[0].Media+'.jpg';
      },
      toFinding(finding) {
        if(finding.Status == 1){
          alert("navigating to inquiry editor");
          return;
        }
        this.$store.dispatch('setFindingIdData', finding.Id)
        this.$router.push({path: '/finding'})
      },
    }
  }
</script>

<style scoped lang="scss">
  #inquiry_header {
    text-align: left;
    margin-left: 20px;
    font-size: 26px;
  }
  #inquiry_main {
    text-align: left;
  }
  .create {
    min-width: 300px;
    min-height: 300px;
    width: 20%;
    display: inline-block;
    // line-height: 160px;
    text-align: center;
    border: 1px;
    border-radius: 10px;
    background-color: #2F2F32;
    color: #FCFCFC;
    margin-left: 16px;
    cursor: pointer;
    div {
      padding: 80px;
      font-size: 36px;
      span {
        font-size: 70px;
      }
    }
  }
  .create:hover{
    background-color: #6F7071;
  }
  .draft-button{
    border-radius: 50px;
    width: 100px;
    font-size: 18px;
    color: white;
    padding: 5px;
    background-color: #2E2F32;
  }
</style>