<template>
  <div id="find_ar">
    <img id="ar-clear" class="clickable" src="@/assets/clear-24-px.png" v-on:click="exit()">
    <div id="ar-wrapper">
      <div id="video-container" >
        <!-- Sensor Connect -->
        <img src="@/assets/img-connect-sensor.png" v-show="!sensor_connect">
        <p v-show="!sensor_connect">Please plug in sensor and click Connect</p>
        <serial v-show="!sensor_connect" @nuvData="nuvData" @nluxData="nluxData" @nsensor_connected="nsensor_connected"></serial>

        <!-- Don't Move Canvas! -->
        <video id="sourcevideo" class="layer" playsinline v-show="sensor_connect"></video>
        <canvas id="targetcanvas" class="layer" v-show="sensor_connect"></canvas>
        <canvas id="uvcanvas" width="128" height="128" style="display:none;"></canvas>
        <canvas id="luxcanvas" width="128" height="128" style="display:none;"></canvas>

        
        <img id="detect-img" src="@/assets/uv-sensor-image.png" v-show="sensor_connect && !marker_detect_first">
        <el-popover
        ref="input_label"
        placement="bottom"
        width="200"
        trigger="hover"
        >
          Help others understand<br/> what youre testing in this<br/> scene with a clear label.
        </el-popover>
        <input ref="print" type="text" name="" v-popover:input_label v-model="ar_label" v-show="sensor_connect && marker_detect_first" placeholder="Label this Scene" :readonly="read">
        <div id="original-data" v-show="sensor_connect && !marker_detect && marker_detect_first">
          <span>UVI: {{uvData}}</span> <br/>
          <span>LUX: {{luxData}}</span>
        </div>
      </div>
      <div id="function-bar" v-show="sensor_connect">
        <img id="snap_button" :src="snap_button" v-on:click="snap()">
        <img id="img_done" src="@/assets/ar_done.png" v-on:click="goOverview()" v-show="next">
        <canvas id="snap_img" width="640" height="400" style="display:none;"></canvas>
      </div>
      <div id="img-preview" v-show="sensor_connect">
        <img :src="img1">
        <img :src="img2">
        <img :src="img3">
      </div>
    </div>
    <div id="scene-notes" v-show="sensor_connect">
      <span><b>Scene Notes</b></span><br/>
      {{time}}
      <textarea name="" v-model="ar_note" placeholder="Add notes to help others recreate this scene accurately when replicating your inquiry." :readonly="read" maxlength="200"></textarea>
    </div>
  </div>
</template>

<script>
  import ARToolkit from 'artoolkit5-js'
  import * as THREE from 'three'
  import serial from '@/components/getSerial'
  let sourceVideo;
let targetCanvas;

// AR controller reference
let arc;

// internal HIRO marker ID
let markerId;

// used to render THREE.js scene
let renderer, scene, camera, markerRoot;

export default {
  data() {
    return {
      sensor_connect: false,
      marker_detect: false,
      marker_detect_first: false,
      uvData: null,
      luxData: null,
      uvSave: null,
      luxSave: null,
      count: null,
      output: null,
      snap_button: 'snap_inactive.png',
      ar_label: null,
      ar_note:null,
      print_src: null,
      img1: null,
      img2: null,
      img3: null,
      img4: null,
      img5: null,
      ar_img: [],
      next: false,
      time: null,
      ar_re: this.$store.state.finding,
      read: false
    }
  },
  components: {
    'serial': serial,
  },
  watch: {
    marker_detect() {
      if (this.ar_label != null && this.ar_label != '' && this.marker_detect == true && this.ar_note != null) {
        this.snap_button = 'snap_active.png'
      } else {
        this.snap_button = 'snap_inactive.png'
      }
    },
    img1() {
      if (this.img1 != null) {
        this.next = true
      }
    }
  },
  created() {
    console.log(this.ar_re);
    if (sessionStorage.connection == 'true') {
      this.sensor_connect = true
      console.log('22')
    }
    if (this.ar_re != null) {
      const type_base = {
        type: this.ar_re.Type,
        base: this.ar_re.Base
      }
      this.$store.dispatch('setArreData', type_base)
      this.ar_label = this.ar_re.Dataset[0].Condition
      this.ar_note = this.ar_re.Notes
      if (this.ar_re.Type == '2') {
        this.read = true
      }
    } else {
      const type_base = {
        type: 1,
        base: null
      }
      this.$store.dispatch('setArreData', type_base)
    }
  },
  mounted() {
    const date = new Date()
    this.time = date.toLocaleString()
    this.initCamera()
    .then(video => {
      // start camera playback
      sourceVideo = video;
      sourceVideo.width = 640;
      sourceVideo.height = 400;
      sourceVideo.play();

      // init target canvas
      this.initTargetCanvas();

      // init THREE renderer
      this.initRenderer();

      return new Promise(resolve => {
        sourceVideo.addEventListener("loadeddata", event => {
          console.log("Camera is ready");
          resolve();
        });
      });
    })
    .then(_ => {
      return this.initAR();
    })
    .then(_ => {
      console.log("AR controller initialized");
      this.startProcessing();
    });
  },
  methods: {
    nuvData(uv) {
      this.uvData = uv
    },
    nluxData(lux) {
      this.luxData = lux
    },
    nsensor_connected(connect) {
      this.sensor_connect = connect
    },
    exit() {
      this.$store.dispatch('setFindingData', null)
      this.$router.push({path: '/home/myinquiries'})
    },
    goOverview() {
      console.log('here')
      this.$store.dispatch('setArimgData', this.ar_img)
      this.$store.dispatch('setArnoteData', this.ar_note)
      this.$store.dispatch('setArlabelData', this.ar_label)
      this.$store.dispatch('setTimeData', this.time)
      this.$store.dispatch('setUVData', this.uvSave)
      this.$store.dispatch('setLUXData', this.luxSave)
      this.$store.dispatch('setFindingData', null)
      this.$router.push({path: '/overview'})
    },
    snap() {
      if (this.snap_button == 'snap_active.png') {
        let vm = this
        const snap_img = document.querySelector('#snap_img')
        const background = document.querySelector('#sourcevideo')
        const ctx = snap_img.getContext('2d')
        ctx.drawImage(background, 0, 0, 640, 400)
        let input_image = new Image()
        let data_image = new Image()
        renderer.render(scene, camera)
        let data_imgData = renderer.domElement.toDataURL("image/png")
        data_image.src = data_imgData
        this.print()
        .then(() => {
          input_image.src = this.print_src
          console.log(input_image.src)
          input_image.onload = function() {
            ctx.drawImage(input_image, 217, 330, 206, 46)
            ctx.fillStyle = "black"
            ctx.font = '20px arial'
            ctx.textAlign = 'center'
            ctx.textBaseline = 'middle'
            ctx.fillText(vm.ar_label, 320, 352)
            ctx.drawImage(data_image, 0, 0, 640, 400)
            if (vm.img1 == null) {
              vm.img1 = snap_img.toDataURL('image/jpg', 1)
              vm.ar_img[0] = vm.img1
            } else if (vm.img2 == null) {
              vm.img2 = snap_img.toDataURL('image/jpg', 1)
              vm.ar_img[1] = vm.img2
            } else {
              vm.img3 = snap_img.toDataURL('image/jpg', 1)
              vm.ar_img[2] = vm.img3
            }
          }
        })
      }
      // console.log(image)
    },
    async print() {
      const el = this.$refs.print;
      // add option type to get the image version
      // if not provided the promise will return 
      // the canvas.
      const options = {
        type: 'dataURL'
      }
      this.print_src = await this.$html2canvas(el, options);
    },
    showData() {
      let data_canvas = document.querySelector('#uvcanvas')
      let ctx = data_canvas.getContext('2d')
      ctx.beginPath()
      ctx.arc(64, 64, 64, 0, Math.PI*2, true);
      ctx.fillStyle = 'white'
      ctx.closePath()
      ctx.fill()
      ctx.fillStyle = "black"
      ctx.font = '24px arial'
      ctx.textBaseline = 'middle'
      this.uvSave = this.uvData
      ctx.fillText(this.uvData, 52, 60)
      ctx.fillStyle = "black"
      ctx.font = '16px arial'
      ctx.textBaseline = 'middle'
      ctx.fillText('UVI', 52, 80)

      data_canvas = document.querySelector('#luxcanvas')
      ctx = data_canvas.getContext('2d')
      ctx.beginPath()
      ctx.arc(64, 64, 64, 0, Math.PI*2, true);
      ctx.fillStyle = 'white'
      ctx.closePath()
      ctx.fill()
      ctx.fillStyle = "black"
      ctx.font = '24px arial'
      ctx.textBaseline = 'middle'
      this.luxSave = this.luxData
      ctx.fillText(this.luxData, 52, 60)
      ctx.fillStyle = "black"
      ctx.font = '16px arial'
      ctx.textBaseline = 'middle'
      ctx.fillText('LUX', 52, 80)
    },
    updateData() {
      this.showData()
      let uvdataMap = new THREE.CanvasTexture(document.querySelector('#uvcanvas'))
      let uvdataMaterial = new THREE.SpriteMaterial( { map: uvdataMap } );
      let uvdataSprite = new THREE.Sprite(uvdataMaterial)

      let luxdataMap = new THREE.CanvasTexture(document.querySelector('#luxcanvas'))
      let luxdataMaterial = new THREE.SpriteMaterial( { map: luxdataMap } );
      let luxdataSprite = new THREE.Sprite(luxdataMaterial)
      luxdataSprite.position.x = 1
      luxdataSprite.position.y = 1

      return [uvdataSprite, luxdataSprite]
    },
    async initCamera() {
      const constraints = {
        audio: false,
        video: {
          //facingMode: "environment",
          facingMode: "user",
          width: 640,
          height: 400,
          frameRate: { max: 30 }
        }
      };

      // initialize video source
      const video = document.querySelector("#sourcevideo");
      const stream = await navigator.mediaDevices.getUserMedia(constraints);
      video.srcObject = stream;

      return new Promise(resolve => {
        video.onloadedmetadata = () => {
          resolve(video);
        };
      });
    },
    async initAR() {
      // init AR controller
      // Note: this camera_para.dat file works well for most built-in laptop webcams
      // It does NOT work very well for newer iPhone models (X / XS / 11)
      // The cube will be off quite a bit.
      arc = await ARToolkit.ARController.initWithDimensions(
        640, 400,
        '/data/ar/camera_para.dat'
      );
      console.log("AR Controller initialized");

      // add marker
      markerId = await arc.artoolkit.addMarker(arc.id, '/data/ar/pattern-sun.patt');
      console.log("HIRO marker added with marker ID", markerId);
    },
    initTargetCanvas() {
      // target canvas should overlap source video
      targetCanvas = document.querySelector("#targetcanvas");
      targetCanvas.width = sourceVideo.width;
      targetCanvas.height = sourceVideo.height;
    },
    initRenderer() {
      // create a scene overlaying the video
      renderer = new THREE.WebGLRenderer({ canvas: targetCanvas, alpha: true });
      renderer.setSize(640, 400);
      renderer.setClearColor(0xffffff, 0);
      renderer.autoClear = false;
      // init camera
      camera = new THREE.Camera();
      camera.matrixAutoUpdate = false;
      scene = new THREE.Scene();
      scene.add(camera);

      markerRoot = new THREE.Group()
      const uvdataSprite  = this.updateData()[0]
      const luxdataSprite  = this.updateData()[1]
      const spriteMap = new THREE.TextureLoader().load( "/data/ar/happy_zally.png" );
      const spriteMaterial = new THREE.SpriteMaterial( { map: spriteMap } );
      const sprite = new THREE.Sprite( spriteMaterial );
      sprite.position.z = 0.5
      sprite.position.x = -1
      sprite.position.y = -1
  
      markerRoot.add(sprite)
      markerRoot.add(uvdataSprite)
      markerRoot.add(luxdataSprite)  
      markerRoot.markerMatrix = new Float64Array(12);
      markerRoot.matrixAutoUpdate = false;
      console.log(markerRoot)
      scene.add(markerRoot)
    },

    startProcessing() {
      const processFrame = () => {
        const result = arc.detectMarker(sourceVideo);
        if(result !== 0) {
          // ARToolkit returning a value !== 0 means an error occured
          console.log('Error detecting markers');
          return;
        }
        // get the total number of detected markers in frame
        const markerNum = arc.getMarkerNum();
        let hiroMarkerNum = false;
        // check if one of the detected markers is the HIRO marker
        for(let i = 0; i < markerNum; i++) {
          const markerInfo = arc.getMarker(i);
          if(markerInfo.idPatt == markerId) {
            // store the marker ID from the detection result
            hiroMarkerNum = i;
            break;
          }
        }

        if(hiroMarkerNum !== false) {
          // HIRO marker found
          if(markerRoot.visible) {
            arc.getTransMatSquareCont(
              hiroMarkerNum, 1, markerRoot.markerMatrix, markerRoot.markerMatrix
            );
          } else {
            arc.getTransMatSquare(
              hiroMarkerNum , 1 /* Marker width */, markerRoot.markerMatrix
            );
          }
          // show marker root
          markerRoot.remove(uvdataSprite)
          markerRoot.remove(luxdataSprite)
          const uvdataSprite = this.updateData()[0]
          const luxdataSprite = this.updateData()[1]
          markerRoot.add(uvdataSprite)
          markerRoot.add(luxdataSprite)
          this.marker_detect = true;
          this.marker_detect_first = true
          markerRoot.visible = true;

          // position camera
          arc.arglCameraViewRHf(
            arc.transMatToGLMat(markerRoot.markerMatrix),
            markerRoot.matrix.elements,
            0.5
          );
        } else {
          // not found
          markerRoot.visible = false;
          this.marker_detect = false;
        }
        // render the scene
        renderer.clear();
        renderer.render(scene, camera);
        // process next frame
        window.requestAnimationFrame(processFrame);
      };
      // initialize camera projection matrix
      const cameraMatrix = arc.getCameraMatrix();
      camera.projectionMatrix.fromArray(cameraMatrix);
      processFrame();
    },
  }
}
</script>

<style>
#ar-clear {
  float: right;
  margin-top: 40px;
  margin-right: 56px;
}
#ar-wrapper {
  width: 640px;
  height: 582px;
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
  #video-container {
  width: 640px;
  height: 400px;
  margin: auto;
  left: 0;
  right: 0;
  border-radius: 5px;
  overflow: hidden;
}

#video-container .layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

#sourcevideo {
  object-fit: cover;
  /*transform: rotateY(180deg);*/
}
#video-container input {
  position: absolute;
  margin-top: 330px;
  left: 210px;
  height: 42px;
  width: 200px;
  border-radius: 5px;
  background-color: #FFF2CD;
  outline: none;
  text-align: center;
  border: 1px solid #FEBF00;
  font-size: 20px;
  color: #0F323C;
}
#detect-img {
  position: absolute;
  top: 0;
  left: 0;
}
#original-data {
  position: absolute;
    width: 200px;
    background: #DDF7F5;
    margin-left: 440px;
  top: 0;
}
#function-bar {
  height: 72px;
  background-color: black;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
#snap_button {
  width: 42px;
  margin-top: 15px;
  cursor: pointer;
  margin-left: 50px;
}
#img_done {
  width: 26px;
  margin-right: 30px;
  float: right;
  margin-top: 23px;
  cursor: pointer;
}
.el-popover {
  font-size: 16px;
  color: #323335;
  font-family: 'Arial';
  text-align: center;
}
#img-preview {
  height: 110px;
  width: 640px;
  border-radius: 5px;
  background-color: #D9DCE2;
}
#img-preview img{
  width: 128px;
  margin-top: 15px; 
  float: left;
  margin-left: 15px;
}
#scene-notes {
  width: 248px;
    height: 470px;
    position: absolute;
    right: 86px;
    top: 98px;
    border-radius: 5px;
    border: 2px solid #323335;
}
#scene-notes span {
  font-size: 20px;
    margin-top: 12px;
    margin-bottom: 12px;
    display: block;
}
#scene-notes textarea {
  height: 155px;
    width: 200px;
    margin-top: 12px;
    border: none;
}
</style>