import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
  	role: window.sessionStorage.getItem('role'),
  	name: window.sessionStorage.getItem('name'),
  	uid: window.sessionStorage.getItem('uid'),
    achivements: window.sessionStorage.getItem('achivements'),
    findingId: window.sessionStorage.getItem('findingId'),
    arimg: null,
    arnote: null,
    arlabel: null,
    arre: null,
    time: null,
    uv: null,
    lux: null,
    connection: null,
    finding: null
  },
  mutations: {
  	setRole(state, raw) {
  		state.role = raw
      window.sessionStorage.setItem('role', raw)
  	},
    setName(state, raw) {
      state.name = raw
      window.sessionStorage.setItem('name',raw)
    },
    setUid(state, raw) {
      state.uid = raw
      window.sessionStorage.setItem('uid',raw)
    },
    setAchivements(state, raw) {
      state.achivements = raw
      window.sessionStorage.setItem('achivements',raw)
    },
    setFindingId(state, raw) {
      state.findingId = raw
      window.sessionStorage.setItem('findingId',raw)
    },
    setArimg(state, raw) {
      state.arimg = raw
    },
    setArnote(state, raw) {
      state.arnote = raw
    },
    setArlabel(state, raw) {
      state.arlabel = raw
    },
    setTime(state, raw) {
      state.time = raw
    },
    setUV(state, raw) {
      state.uv = raw
    },
    setLUX(state, raw) {
      state.lux = raw
    },
    setConnection(state, raw) {
      state.connection = raw
      window.sessionStorage.setItem('connection',raw)
    },
    setFinding(state, raw){
      state.finding = raw
      window.sessionStorage.setItem('finding',raw)
    },
    setArre(state, raw) {
      state.arre = raw
    },
  },
  actions: {
    setRoleData({commit}, raw) {
      commit('setRole', raw)
    },
    setNameData({commit}, raw) {
      commit('setName', raw)
    },
    setUidData({commit}, raw) {
      commit('setUid', raw)
    },
    setAchivementsData({commit}, raw) {
      commit('setAchivements', raw)
    },
    setFindingIdData({commit}, raw) {
      commit('setFindingId', raw)
    },
    setArimgData({commit}, raw) {
      commit('setArimg', raw)
    },
    setArnoteData({commit}, raw) {
      commit('setArnote', raw)
    },
    setArlabelData({commit}, raw) {
      commit('setArlabel', raw)
    },
    setTimeData({commit}, raw) {
      commit('setTime', raw)
    },
    setUVData({commit}, raw) {
      commit('setUV', raw)
    },
    setLUXData({commit}, raw) {
      commit('setLUX', raw)
    },
    setConnectionData({commit}, raw) {
      commit('setConnection', raw)
    },
    setFindingData({commit}, raw){
      commit('setFinding', raw)
    },
    setArreData({commit}, raw){
      commit('setArre',raw)
    },
  },
  modules: {
  }
})
