<template>
	<div>
    <el-row>
      <el-col :span="6" v-for="finding in raw" :offset="1" v-bind:key="finding.Id">
        <el-card class="box-card" shadow="hover" :body-style="{ padding: '0px' }" @click.native="toFinding(finding.Id)">
          <img v-bind:src="'https://kiwrious.org/Storage/'+ getMedia(finding)">
          <div class='box-description'>
            <div class="box-user"><i class="el-icon-user-solid"></i>{{finding.UserNickname}}</div>
            <div class="box-name">{{finding.Name}}</div>
          </div>
        </el-card>
      </el-col>
    </el-row>  
  </div>
</template>

<script>
	import axios from 'axios'

  var storage = [];
  const api = axios.create({
    baseURL: 'https://kiwrious.org/api',
  })
  export default {
    data() {
      return {
        raw: null,
      }
    },
    beforeRouteEnter(to, from, next) {
      const url = '/finding/inquiry/948a06f5-5f03-498a-9258-3f89ea1e2362'
      api.get(url)
      .then((response) => {
        storage = [];
        response.data.forEach(f=>{
          if(f.Type==1){
            storage.push(f);
          }
        });
        next()
      })
      .catch((error) => {
        console.log(error)
        this.$message({
          message: 'Network Error, Please try again',
          type: 'error'
        })
      })
    },
    created() {
      this.raw = storage;
    },
    methods: {
      toFinding(fid) {
        this.$store.dispatch('setFindingIdData', fid)
        this.$router.push({path: '/finding'})
      },
      getMedia(finding){
        if(finding.Dataset[0].Media == null){
          return "default-finding-image.jpg";
        }
        return finding.Dataset[0].Media+'.jpg';
      }
    }
  }
</script>

<style lang="scss">
  .box-card {
    margin-bottom:20px;
    width: 110%;
    border-radius: 15px !important;
    // outline: 1px solid red;
    box-shadow: 1px 1px 1px grey;
    cursor: pointer;
    img {
      width: 100%;
    }
  }
  .box-description {
      text-align: left;
      padding: 12px;
      border-radius: 10px;
      .box-user {
        font-size: 22px;
        margin-bottom: 10px;
        display: inline-block;
        width: 85%;
        position: relative;
      }
      .box-name {
        margin-left: 45px;
        font-size: 25px;
        font-weight: bold;
      }
    }
</style>