<template>
  <div id="login_div">
    <el-row :gutter="12">
      <el-col :span="8" :offset="8">
        <el-card shadow="always">
          <div id="login_card">
            <img src="@/assets/login_title.png">
            <p><b>Username or email address</b></p>
            <el-input v-model="account"></el-input>
            <p><b>Password</b></p>
            <el-input v-model="password" show-password></el-input>
            <!-- <div class="error"><span>{{password_message}}</span></div> -->
            <el-button round @click="check">Sign in</el-button>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
	import axios from 'axios'
  const api = axios.create({
    baseURL: 'https://kiwrious.org/api',
  })
  export default {
    props:['id'],
    created(){

    },
    data() {
      return {
        account: '',
        password: '',
        password_message: '',
      }
    },
    methods: {
      check() {
        if (this.account == '' || this.password == '') {
          this.$message({
            message: 'Please enter account and password',
            type: 'error'
          })
        } else {
          const url = '/user/' + this.account
          api.get(url)
          .then((response) => {
            if (response.status === 200) {
              if (this.password == response.data.Password) {
                if(this.id){
                  this.$router.push({path: '/finding/'+this.id});
                  return;
                }
                this.$store.dispatch('setRoleData', response.data.Role)
                this.$store.dispatch('setUidData', response.data.Id)
                this.$store.dispatch('setNameData', response.data.Name)
                this.$store.dispatch('setAchivementsData', response.data.achivements)
                this.$router.push({path: '/home/myinquiries'})
              } else {
                this.$message({
                  message: 'Wrong Password',
                  type: 'error'
                })
              }
            }
            console.log(response)
          })
          .catch((error) => {
            if (error.message == 'Request failed with status code 404') {
              const e = 'Please check your username'
              this.$message({
                message: e,
                type: 'error'
              })
            }
            console.log(error.message)
          })
        }
      }
    },
  }
</script>

<style lang="scss">
  #login_div {
    margin-top: 180px;
  }
  #login_card {
    text-align: center;
    p {
      margin: 0; 
    }
    img {
      height: 140px;
    }
    .el-input {
      margin-bottom: 10px;
    }
    .el-input__inner {
      width: 70%;
      background-color: white !important;
      border-width: 0px 0px 1px 0px;
      text-align: center;
    }
    .el-button {
      margin-top: 20px;
      width: 50%;
      background-color: #178de7;
      color: #fff;
    }
    .el-button:hover{
      background-color: #1372ba;
    }
    .error {
      color: red;
    }
  }
</style>